import React, { Suspense } from 'react';
import { Outlet, useLocation } from 'react-router-dom';
import { useApp } from '../contexts/AppContext';
import ModernHeader from '../components/ModernHeader';
import PlatformSupportBanner from '../components/PlatformSupportBanner';
import { LoadingState } from '../components/EmptyStates';
import { useTranslation } from 'react-i18next';

const AppLayout = () => {
  const location = useLocation();
  const { darkMode } = useApp();
  const { t } = useTranslation();
  
  // Don't show header on login page
  const showHeader = location.pathname !== '/login';

  return (
    <div className={`min-h-screen transition-colors ${darkMode ? 'bg-gray-900' : 'bg-gray-50'}`}>
      {/* Platform Support Banner */}
      <PlatformSupportBanner />
      
      {/* Header */}
      {showHeader && <ModernHeader />}

      {/* Main Content */}
      <main id="main-content" className="focus:outline-none" tabIndex="-1">
        <Suspense fallback={<LoadingState message={t('common.loading')} />}>
          <Outlet />
        </Suspense>
      </main>

      {/* Footer */}
      {showHeader && (
        <footer className={`${darkMode ? 'bg-gray-800 border-gray-700' : 'bg-white border-gray-200'} border-t mt-auto`}>
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              <div>
                <div className="flex items-center mb-4">
                  <span className={`font-bold ${darkMode ? 'text-white' : 'text-gray-900'}`}>{t('app.name')}</span>
                </div>
                <p className={`${darkMode ? 'text-gray-300' : 'text-gray-600'} text-sm`}>
                  {t('footer.description')}
                </p>
              </div>
              
              <div>
                <h3 className={`font-semibold ${darkMode ? 'text-white' : 'text-gray-900'} mb-4`}>{t('footer.quickLinks')}</h3>
                <ul className="space-y-2 text-sm">
                  <li><a href="/home" className={`${darkMode ? 'text-gray-300 hover:text-blue-400' : 'text-gray-600 hover:text-blue-600'} transition-colors`}>{t('navigation.browse')}</a></li>
                  <li><a href="/items/add" className={`${darkMode ? 'text-gray-300 hover:text-blue-400' : 'text-gray-600 hover:text-blue-600'} transition-colors`}>{t('footer.postItem')}</a></li>
                  <li><a href="/profile" className={`${darkMode ? 'text-gray-300 hover:text-blue-400' : 'text-gray-600 hover:text-blue-600'} transition-colors`}>{t('footer.myAccount')}</a></li>
                </ul>
              </div>
              
              <div>
                <h3 className={`font-semibold ${darkMode ? 'text-white' : 'text-gray-900'} mb-4`}>{t('footer.support')}</h3>
                <ul className="space-y-2 text-sm">
                  <li><span className={`${darkMode ? 'text-gray-300' : 'text-gray-600'}`}>{t('contact.platformSupport')}</span></li>
                  <li><span className={`${darkMode ? 'text-gray-300' : 'text-gray-600'}`}>{t('footer.email')}</span></li>
                  <li><span className={`${darkMode ? 'text-gray-300' : 'text-gray-600'}`}>{t('footer.available24x7')}</span></li>
                </ul>
              </div>
            </div>
            
            <div className={`border-t ${darkMode ? 'border-gray-700' : 'border-gray-200'} pt-6 mt-6 text-center`}>
              <p className={`text-sm ${darkMode ? 'text-gray-400' : 'text-gray-500'}`}>
                {t('footer.copyright')} | 
                <a href="/privacy" className={`ml-1 hover:underline ${darkMode ? 'text-blue-400 hover:text-blue-300' : 'text-blue-600 hover:text-blue-800'} transition-colors`}>
                  {t('legal.privacyPolicy')}
                </a> | 
                <a href="/terms" className={`ml-1 hover:underline ${darkMode ? 'text-blue-400 hover:text-blue-300' : 'text-blue-600 hover:text-blue-800'} transition-colors`}>
                  {t('legal.termsOfService')}
                </a>
              </p>
            </div>
          </div>
        </footer>
      )}
    </div>
  );
};

export default AppLayout;
