import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';

const AppContext = createContext(null);

export const useApp = () => {
  const context = useContext(AppContext);
  if (!context) {
    throw new Error('useApp must be used within AppProvider');
  }
  return context;
};

export const AppProvider = ({ children }) => {
  const [userRole, setUserRole] = useState('guest');
  const [currentUser, setCurrentUser] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [items, setItems] = useState([]);
  const [users, setUsers] = useState([]);
  const [darkMode, setDarkMode] = useState(false);
  const [loading, setLoading] = useState(false);

  // Check authentication on mount
  useEffect(() => {
    const checkAuth = async () => {
      const { authHelpers } = await import('../services/api');
      if (authHelpers.isAuthenticated()) {
        const user = authHelpers.getUser();
        if (user) {
          setIsLoggedIn(true);
          
          // Force correct role detection for organization admin accounts
          let userRole = user.role;
          if (user.email && (
            user.email === 'admin@aau.edu.et' || 
            user.email === 'admin@hu.edu.et' || 
            user.email.includes('admin@') ||
            userRole === 'orgAdmin' ||
            userRole === 'org_admin'
          )) {
            if (!userRole || userRole === 'user') {
              userRole = 'orgAdmin';
            }
          }
          
          setUserRole(userRole);
          setCurrentUser(user);
        }
      }
    };
    checkAuth();
  }, []);

  // Apply dark mode
  useEffect(() => {
    if (darkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [darkMode]);

  const handleLogin = useCallback(async (role, user) => {
    const { authHelpers } = await import('../services/api');
    
    // Force correct role for organization admin accounts
    let finalRole = role;
    if (user?.email && (
      user.email === 'admin@aau.edu.et' || 
      user.email === 'admin@hu.edu.et' || 
      user.email.includes('admin@') ||
      role === 'orgAdmin' ||
      role === 'org_admin'
    )) {
      if (!role || role === 'user') {
        finalRole = 'orgAdmin';
      }
    }
    
    // Update user role in the user object if it was corrected
    if (finalRole !== user.role) {
      user = { ...user, role: finalRole };
      authHelpers.saveUser(user);
    }
    
    setUserRole(finalRole);
    setCurrentUser(user);
    setIsLoggedIn(true);
  }, []);

  const handleLogout = useCallback(async () => {
    const { authHelpers } = await import('../services/api');
    authHelpers.removeToken();
    authHelpers.removeUser();
    setUserRole('guest');
    setCurrentUser(null);
    setIsLoggedIn(false);
  }, []);

  const toggleDarkMode = useCallback(() => {
    setDarkMode(prev => !prev);
  }, []);

  const value = {
    userRole,
    setUserRole,
    currentUser,
    setCurrentUser,
    isLoggedIn,
    setIsLoggedIn,
    items,
    setItems,
    users,
    setUsers,
    darkMode,
    setDarkMode,
    toggleDarkMode,
    loading,
    setLoading,
    handleLogin,
    handleLogout
  };

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};
