import React, { useState, useEffect } from 'react';
import { Phone, Clock } from 'lucide-react';
import { platformSettingsAPI } from '../services/api';

const PlatformSupportBanner = () => {
  const [settings, setSettings] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadSettings = async () => {
      try {
        const response = await platformSettingsAPI.getPublic();
        if (response.success && response.data) {
          setSettings(response.data);
        }
      } catch (error) {
        console.error('Failed to load platform support settings:', error);
      } finally {
        setLoading(false);
      }
    };
    loadSettings();
  }, []);

  if (loading || !settings || !settings.enabled) {
    return null;
  }

  return (
    <div className="bg-blue-600 text-white py-2 px-4">
      <div className="max-w-7xl mx-auto flex items-center justify-center space-x-6 text-sm">
        <div className="flex items-center">
          <Phone size={16} className="mr-2" />
          <span className="font-medium">
            Platform Support: {settings.phoneNumber}
          </span>
        </div>
        {settings.is24x7 && (
          <div className="flex items-center">
            <Clock size={16} className="mr-2" />
            <span className="font-medium">24/7 Service</span>
          </div>
        )}
      </div>
    </div>
  );
};

export default PlatformSupportBanner;
